import React from 'react';
import classes from './User.module.css';
const User = (props) => {
  console.log(props.isLoad);
  return (
    <div className='container'>
      {props.isLoad ? (
        <div style={{ width: '100%' }}>
          <img
            style={{ width: '100%' }}
            src='https://flevix.com/wp-content/uploads/2020/01/Bounce-Bar-Preloader-1.gif'
            alt=''
          />
        </div>
      ) : (
        <>
          <div className={classes.User}>
            <div className={classes.Profile}>
              <img src={props.user.avatar_url} alt='' />
            </div>
            <div className={classes.Data}>
              <div>
                <h3>{props.user.name}</h3>
                <div className={classes.Buttons}>
                  <button>
                    Followers <span>{props.user.followers}</span>
                  </button>
                  <button>
                    Following <span>{props.user.following}</span>
                  </button>
                  <button>
                    Public Repos <span>{props.user.public_repos}</span>
                  </button>
                  <button>
                    Public Gists <span>{props.user.public_gists}</span>
                  </button>
                </div>
              </div>
            </div>
          </div>
          <div>
            {props.repos.map((repo) => {
              return <div className={classes.Repo}>{repo.name}</div>;
            })}
          </div>
        </>
      )}

      <button className={classes.Back} onClick={props.back}>
        BACK
      </button>
    </div>
  );
};

export default User;
