import React from 'react';
import classes from './Card.module.css';
const Card = (props) => {
  return (
    <div className={classes.Card} onClick={() => props.getUser(props.login)}>
      {props.isLoading || !props.id ? (
        <img
          src='https://flevix.com/wp-content/uploads/2020/01/Bounce-Bar-Preloader-1.gif'
          alt=''
        />
      ) : (
        <>
          <img src={props.avatar_url} alt={props.id} />
          <h3>{props.login}</h3>
        </>
      )}
    </div>
  );
};

export default Card;
