import React, { Component } from 'react';
import Navbar from '../components/Navbar/Navbar';
import Search from '../components/Search/Search';
import Cards from '../components/Cards/Cards';
import axios from 'axios';
import User from '../components/User/User';
class App extends Component {
  state = {
    users: [],
    text: '',
    isLoading: false,
    user: {},
    repos: [],
    isLoad: false,
  };
  back = (e) => {
    this.setState({ user: {}, isLoad: false });
  };

  onChange = (e) => {
    this.setState({ text: e.target.value });
  };
  async componentDidUpdate(_, preState) {
    if (this.state.text !== preState.text && this.state.text !== '') {
      this.setState({ isLoading: true });
      const res = await axios.get(
        `https://api.github.com/search/users?q=${this.state.text}`
      );
      this.setState({ isLoading: false });
      const users = res['data']['items'];
      this.setState({ users: users });
    }
  }
  getUser = async (user) => {
    if (user) {
      this.setState({ isLoad: true });
      const userData = await axios.get(`https://api.github.com/users/${user}`);
      const repos = await axios.get(
        `https://api.github.com/users/${user}/repos?per_page=5&sort=created:asc`
      );
      this.setState({ user: userData, repos: repos.data, isLoad: false });
    }
  };
  render() {
    let showCards;
    if (this.state.text === '') {
      showCards = null;
    } else {
      showCards = (
        <Cards
          users={this.state.users}
          isLoading={this.state.isLoading}
          getUser={this.getUser}
        />
      );
    }
    return (
      <>
        <Navbar />
        {!this.state.isLoad && Object.keys(this.state.user).length === 0 ? (
          <>
            <Search text={this.state.text} changed={this.onChange} />
            {showCards}
          </>
        ) : (
          <User
            repos={this.state.repos}
            user={this.state.user.data}
            back={this.back}
            isLoad={this.state.isLoad}
          />
        )}
      </>
    );
  }
}

export default App;
